 
.card{
    width: 350px;
    box-shadow: 0px 3px 3px 3px rgb(202, 202, 202);

}
.searchBar{
    display: flex;
    justify-content: center;
    margin-top: 44px;
    background-color: black;
    padding: 22px;
}
.searchBar input{
    width: 600px;
    padding: 9px;
    font-size: 22px;
    border: 1px solid black;
    border: none;
    border-radius: 5px;
}
.cards{
    width: 1100px;
    margin: auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 22px;
}
.card{
    margin-top: 42px;
    
}
.card img{
    width: 100%;
    height: 300px;
}
.card p{
    margin-top: -8px;
    padding: 11px;
    font-size: 22px;
}